//
import '../styles/usage/page/app.scss';

//
import Vue from './libs/vue.js';
import VueRouter from './libs/vue-router.js';

//
import welcome from './components/welcome.vue';
import signin from  './components/signin.vue';
import index from  './components/index.vue';
import search from './components/search/search.vue';
import searchHeroTime from './components/search/searchHeroTime.vue';
import heroData from './components/search/heroData.vue';
import freeHero from './components/search/freeHero.vue';
import allHero from './components/search/allHero.vue';
import myHero from './components/search/myHero.vue';



Vue.use(VueRouter);
let router = new VueRouter();
let App = Vue.extend({});
router.map({
        '/': {
            component: welcome  
        },
       	'/signin':{
       		  component:signin
       	},
       	'/index':{
       		  component:index
       	},
        '/search':{
            component:search
        },
        '/searchHeroTime':{
            component:searchHeroTime
        },
        '/heroData':{
            component:heroData,
            subRoutes: {
              '/': {
                  component: freeHero
                }
            }
          },
        '/freeHero':{
            component:freeHero
        },
        '/allHero':{
            component:allHero
        },
        '/myHero':{
            component:myHero
        }
    })


router.start(App, 'body') 
